import { PathReporter } from 'io-ts/lib/PathReporter';
import { isLeft } from 'fp-ts/lib/Either';
import log from '../../logger';
import { UserSettings } from '../../models/UserSettings';
import { ServerLookupResponse } from '../../models/ServerLookupResponse';
const bo = chrome;
const ifValid = (codec) => (cb) => (x) => {
    const v = codec.decode(x);
    if (isLeft(v)) {
        const msg = `Error decoding backend response:\n${PathReporter.report(v).join('\n')}`;
        log.error(msg);
    }
    else {
        cb(v.right);
    }
};
// this method doesn't do much, but it forces me
// to send messages to the background using
// the correct message type
const sendMessage = (message, cb) => bo.runtime.sendMessage(message, cb);
export const localLookup = (cb) => sendMessage({
    type: 'LocalLookup',
    payload: {
        userId: 'local',
    },
}, ifValid(UserSettings)(cb));
export const serverLookup = (payload, cb) => sendMessage({
    type: 'ServerLookup',
    payload,
}, ifValid(ServerLookupResponse)(cb));
export const configUpdate = (payload, cb) => sendMessage({
    type: 'ConfigUpdate',
    payload,
}, ifValid(UserSettings)(cb));
