import { countBy } from 'lodash';
import config from '../config';
import { getTimeISO8601 } from '../utils';
import log from '../logger';
const bo = chrome;
const INTERVAL = config.FLUSH_INTERVAL;
const now = () => getTimeISO8601();
const state = {
    incremental: 0,
    content: [],
};
function handleVideo(e) {
    state.content.push(Object.assign(Object.assign({}, e.payload), { clientTime: now(), type: 'video', incremental: state.incremental }));
    state.incremental++;
}
function handleSuggested(e) {
    state.content.push(Object.assign(Object.assign({}, e.payload), { incremental: state.incremental, clientTime: now(), type: 'suggested' }));
    state.incremental++;
}
function handleSearch(e) {
    state.content.push(Object.assign(Object.assign({}, e.payload), { incremental: state.incremental, clientTime: now(), type: 'search' }));
    state.incremental++;
}
function sync(hub) {
    if (state.content.length) {
        log.info.strong(`synchronizing ${state.content.length} new items (total since beginning of session: ${state.incremental})`, countBy(state.content, 'type'));
        // Send timelines to the page handling the communication with the API.
        // This might be refactored using something compatible to the HUB architecture.
        bo.runtime.sendMessage({
            type: 'sync',
            payload: state.content,
            userId: 'local',
        }, (response) => hub.dispatch({
            type: 'SyncResponse',
            payload: response,
        }));
        state.content = [];
    }
}
export function register(hub) {
    hub
        .on('NewVideo', handleVideo)
        .on('Suggested', handleSuggested)
        .on('Search', handleSearch)
        .on('WindowUnload', () => sync(hub));
    window.setInterval(() => sync(hub), INTERVAL);
}
